
@keyframes shake {
    5% {
      transform: translate3d(-1px, 0, 0);
    }
    8% {
      transform: translate3d(3px, 0, 0);
    }
  
    11% {
      transform: translate3d(-5px, 0, 0);
    }
    14%{
      transform: translate3d(5px, 0, 0);
    }
    17% {
        transform: translate3d(0px, 0, 0);
    }
    100%{
        transform: translate3d(0px, 0, 0);
    }
}

@keyframes shakeold {
    5%, 45% {
      transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
  }


